.wcmenucart-text {
  display: none; }

/* Header Cart > Vertical Menu */
@media only screen and (min-width: 960px) {
  body.wpex-has-vertical-header .wcmenucart-text {
    display: inline-block;
    padding-left: 20px; }
  body.wpex-has-vertical-header .wcmenucart-count .wcmenucart-details {
    top: 0; } }

/*------------------------------------------------------------------
Globals
-------------------------------------------------------------------*/
/* price color */
